<template>
  <tr>
    <td class="text-xs-center" >
      {{item.name}}
    </td>
    <td class="text-xs-center ">
      {{item.design}}
      <ProductWithDesign
        :product="product"
        :design="currentDesign"
        :name="item.name"
        :currentColorIndex="currentVariant && currentColorIndex"
        :currentColorLightness="currentVariant && currentVariant.color && currentVariant.color.lightness"
      />
    </td>
    <td class="text-xs-center ">
      {{product.name}}
    </td>
    <td class="text-xs-center ">
      {{item.price}}
    </td>
  </tr>
</template>
<script>
  import ProductWithDesign from './ProductWithDesign';

  export default {
    name: 'OrderItemRow',
    props: ['item', 'product'],
    components: { ProductWithDesign },
    computed: {
      currentVariant() {
          return this.product.variants.find((item) => item.id === this.item.variant);
      },
      currentDesign() {
          return {
            activeImage: this.item.design_images[0].image_url,
            fromOrder: true
          };
      },
      currentColorIndex() {
        let colorId = this.currentVariant && this.currentVariant.color.id;
        if (!colorId) return;
        let currentColorIndex = this.product.colors.findIndex((item) => item.id === colorId);
        return currentColorIndex;
      }
    }
  }
</script>
